import { Reducer } from 'redux';

import * as Actions from '../../actions';
import * as Types from '../../actions/actionTypes';

interface PostContentsProps {
    post: {
        postTitle: string
        postDate: string
        postAuthor: string
        postContent: string
        postTags: Array<{ idx: number, name: string }>
    }
}

const tempContents = `
<h3> h3</h3>
<p>
       p    p  .<br />
          <br />
     
</p>
<img src="https://github.com/yoonjonglyu/myHome/blob/master/src/assets/images/sam-test.jpg" alt=" img src  " />
<p>
     .  p  .
</p>
<pre className="post-code"><span>code : js</span><br/><code>const</code> foo = bar;<br /><fc>console.log(foo);</fc></pre>
<p>
     pre  p   class       .
</p>`;

const initialState: PostContentsProps = {
    post: {
        postTitle: "  ",
        postDate: "2020-12-04",
        postAuthor: "ISA()",
        postContent: tempContents,
        postTags: [
            {
                idx: 1,
                name: ""
            },
            {
                idx: 2,
                name: ""
            },
            {
                idx: 3,
                name: ""
            }
        ]
    }
}
const BlogPost: Reducer<PostContentsProps, Actions.PostContentsAction> = (state = initialState, action) => {
    if (action.type === Types.POST_CONTENTS) {
        return {
            ...state,
            post: action.payload
        };
    } else {
        return state;
    }
};

export default BlogPost;